body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 300px;
  min-height: 200px;
  background-color: rgb(133,68,66);
  border-radius: 2px;
}

body::-webkit-scrollbar {
  width: 6px;
  background-color: rgb(60,47,47);
}

body::-webkit-scrollbar-track {
  border-radius: 1px;
}

body::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: rgb(255,244,230);
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.center {
  text-align: center;
}

#searchbar {
    background-color: rgb(190,155,123);
    color: rgb(255,244,230);
    border: 0px;
    margin: 0px;
    border-radius: 3px;
    margin-top: 10px;
}

#searchbar:focus {
   outline-color: rgb(255,255,230);
}
.loading {
    -webkit-transform: scale(.3);
            transform: scale(.3);
}
.center-results {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: rgb(255,244,230)
}
.buttonContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.nextButton, .previousButton {
    border-radius: 10px;
    background-color: rgba(0,0,0,0);
    color: rgb(255,244,230);
    border: 2px solid rgb(255, 244, 230);
    padding: 15px 30px;
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
    cursor: pointer;
    margin: 15px 5px 30px;
}

.nextButton:hover, .previousButton:hover {
    background-color: rgb(255,244,230);
    color: rgb(133,68,66);
    border-color: rgb(133,68,66);
}

.result-div {
    background-color: rgb(255,244,230);
    border-radius: 4px;
    width: 200px;
    margin: 10px;
    color: rgb(60,47,47);
    
}

.result-list {
    list-style-type: none;
    text-align: center;
    padding: 0 5px 5px 5px;
}

li {
    margin: 15px 0;
}

.titleelement{
    color: rgb(84, 105, 119)
}

.authorelement{
    color: rgb(94, 96, 129)
}

.resultnumber {
    margin-top: 10px;
}

.download {
    color: rgb(75,56,50);
}
